
<template>
  <div class="case">
    <Title :title="'中国病例'" />
    <titles :titles="titles" v-on:listsortType="sortType" :colorArr="colorArr"/>
    <div class="content">
      <div v-for="(item,index) in list" :key="index" class="list" @click="openList(index)">
        <div class="list-box">
          <div class="first-box first-floor d-flex d-cont-between">
            <p class="d-flex d-flex-column c1">
              <span 
              :class="item.ename!== 'xianggang' && item.ename!== 'aomen' && item.ename!== 'taiwan'?'nameColor':''">
              {{item.name}}
            </span>
              <span :style="item.jwsr ?'visibility:visible':'visibility:hidden'" class="jwsr">{{item.jwsr || '含境外输入'}}</span>
            </p>
            <p class="c2">
              <span>{{item.econNum}}</span>
            </p>
            <p class="c3">
              <span>{{item.value}}</span>
            </p>
            <p class="c4">
              <span>{{item.deathNum}}</span>
            </p>
            <p class="c5">
              <span>{{item.cureNum}}</span>
            </p>

            <p class="c6">
              <i v-if="item.city.length" 
              :class=" item.show ? 'rotate iconfont icon-xiala2':'righting iconfont icon-xiala2'"></i>
            </p>
          </div>
          <div class="two-dime" v-if="item.show">
              <ul>
                <li class="first-box first-floor d-flex d-cont-between" v-for="city in item.city">
                  <p style="width:17.95%;text-align: center;" class="d-flex d-flex-column c1">
                    <span>{{city.name}}</span>
                    <span v-if="city.jwsr" class="jwsr">{{city.jwsr}}</span>
                  </p>
                  <p class="c2">
                    <span>{{city.econNum}}</span>
                  </p>
                  <p class="c3">
                    <span>{{city.conNum}}</span>
                  </p>
                  <p class="c4">
                    <span>{{city.deathNum}}</span>
                  </p>
                  <p class="c5">
                    <span>{{city.cureNum}}</span>
                  </p>
                  <p class="c6"></p>
                </li>
              </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { SinaData } from '@/api/indexCovid';
import Title from '@/components/title';
import titles from '@/components/titles';
export default {
  data() {
    return {
      list: [],
      colorArr:['','#FF9D99','#E07979','#AAAAAA','#7BD6C1',''],
      titles: [
        { name: '省市', type: 0, bgColor: '#eaeaea' ,isclick: false,loading:false},
        { name: '现存确诊', type: 1, diff: 0, bgColor: '#FFE2DC', isclick: true ,loading:false},
        { name: '累计确诊', type: 2, diff: 0, bgColor: '#FFC9C9', isclick: true ,loading:false},
        { name: '死亡', type: 3, diff: 0, bgColor: '#eaeaea', isclick: true ,loading:false},
        { name: '治愈', type: 4, diff: 0, bgColor: '#C0ECE0', isclick: true ,loading:false},
        { name: '', type: 5, bgColor: '#eaeaea' ,isclick: false,loading:false},
      ],
      type:1
    }
  },
  watch:{
    titles:{
      immediate:true,
      deep:true,
      handler(newval){
        if(newval[this.type].diff === 0) return;
        let actions = {
          '1':()=>{
            newval[this.type].diff === 1 ? this.list.sort((a,b)=>a.econNum - b.econNum) : this.list.sort((a,b)=>b.econNum - a.econNum)
          },
          '2':()=>{
            newval[this.type].diff === 1 ? this.list.sort((a,b)=>b.value - a.value) : this.list.sort((a,b)=>a.value - b.value)
          },
          '3':()=>{
            newval[this.type].diff === 1 ? this.list.sort((a,b)=>b.deathNum - a.deathNum) : this.list.sort((a,b)=>a.deathNum - b.deathNum)
          },
          '4':()=>{
            newval[this.type].diff === 1 ? this.list.sort((a,b)=>b.cureNum - a.cureNum) : this.list.sort((a,b)=>a.cureNum - b.cureNum)
          }
        }
        actions[this.type].call(this);
      }
    }
  },
  components: {
    Title,
    titles
  },
  mounted() {
    this.getCaseData()
  },
  methods: {
    async getCaseData() {
      let res = await SinaData();
      res.data.list.forEach(item => item.show = false);
      this.titles.forEach(item => item.loading = true);
      this.list = res.data.list;
    },
    openList(e){
      this.list.forEach((item,index) =>e===index && (item.show = !item.show))
    },
    sortType(e){
      if(!e.isclick) return;
      this.titles.forEach(item =>{
        if(e.type === item.type){ 
          item.diff === 0 || item.diff===2 ? item.diff = 1: (item.diff === 1 ?item.diff = 2:1 )
        }
      })
      this.type = e.type;
    }
  },
}
</script>
<style lang="less" scoped>
.nameColor{
  color: #4D79F3;
}


.case {
  width: 714px;
  background-color: #FFF;
  margin: 0 auto;
  margin-top: 30px;



  .content {
    .list {
      padding: 0 10px;
      .list-box{
        padding:22px 0;
        .two-dime{
        background-color: #FFF;
        ul{
          li{
            border-bottom:2px solid #f5f5f5;
            padding:10px 0;
            line-height: 32px;
            position: relative;
            flex-direction: row;
            height:90px;
            
          }
        }
      }
      }
    }
    .list:nth-child(odd){
        background-color: #F9FAF9;
      }
    .first-floor {
      p {
        display:inline-block;
        flex: 1 1 0;
        text-align: center;
        
        .jwsr{
          font-size:20px;
          display:block;
          margin-top:10px;
          color: #aaa;
          
        }
      }

      .c1{ 
        flex:none;
        text-align: left
      }
      .c6{
        flex: none;
        width:5%;
        i{
          margin-top:20px;
          display: block;
          color: #4169E2;
        }
        .rotate{
          animation: rotate180 0.3s forwards;
        }
        .righting{
          animation: restore 0.3s forwards;
        }
        @keyframes rotate180 {
          0%{-webkit-transform:rotate(0deg);}
          100%{-webkit-transform:rotate(-180deg);}
        }
        @keyframes restore {
          0%{-webkit-transform:rotate(-180deg);}
          100%{-webkit-transform:rotate(0);}
        }
      }
    }
  }
}
</style>