<template>
  <div class="wrap">
    <van-swipe class="my-swipe height" :touchable="false" :autoplay="3000" indicator-color="white" :vertical="true"
        :loop="true">
      <van-swipe-item v-for="(item, index) in list">
        <div class="anti-epidemic d-flex height d-cont-between">
          <div class="anti-epidemic-time height">{{item.cTime | getTimestamp}}</div>
          <div class="anti-epidemic-title height d-flex d-cont-start align-item"><span>{{ item.title }}</span></div>
        </div>
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
import '@/utils/filters';
import { country_epidemic_lists } from '@/api/index';
export default {
  data() {
    return {
      list: [],
    }
  },
  mounted() {
    this.getProgress();
  },
  methods: {
    async getProgress() {
      let res = await country_epidemic_lists();
      this.list = res.data
    }
  },
}
</script>
<style lang="less" scoped>
.height{
  height:100%;
}
.wrap {
  height:150px;
  position: relative;
  background-color: #FFF;
  width: 714px;
  margin: 0 auto;
  margin-top: 30px;
  overflow: hidden;
  .anti-epidemic {
    overflow: hidden;

    .anti-epidemic-time {
      width: 143px;
      line-height:200px;
      text-align:right;
      font-size:32px;
      color: #333333;
      position: relative;
    }
    .anti-epidemic-time::after{
      content: '';
      width:2px;
      height:60px;
      background-color:rgba(228, 228, 228, 0.6);
      position: absolute;
      right:-55px;
      top:50%;
      transform:translateY(-50%);
    }

    .anti-epidemic-title {
      width:480px;
      font-size:32px;
      color: #333333;
      letter-spacing: 0;
      line-height: 46px;
      padding-right:40px;
      position: relative;
      overflow: hidden;
    }
  }
}
.wrap::after{
  content: '';
  width: 143px;
  height:58px;
  background: url('@/assets/images/anti-epidemic-ing.png');
  background-size: cover;
  position: absolute;
  left:20px;
  top:30px;
}
.wrap::before{
  font-family: 'iconfont';
  content: '\e8f1';
  right:10px;
  top:50%;
  transform:translateY(-50%);
  position: absolute;
  font-size:40px;
  color:#4d79f3;

}
</style>