<template>
  <div class="main main-center">
    <div class="main-title-topTen">
      <slot name="title"></slot>
      <div v-if="cptType==0" id="topTen"></div>
      <template v-else>
        <div v-show="type !== 1" id="topSerious"></div>
        <div v-show="type == 1" id="five-region"></div>
      </template>
    </div>
    <div class="tbas d-flex"> 
      <p 
      :class="type===item.type?'active':''"
      v-for="(item ,index) in topTenTabs" 
      @click="checkType(item.type)"
      >
        {{item.text}}
      </p>
    </div>
  </div>
</template>

<script>

export default {
  props:{
    topTenTabs:{
      type:Array,
      default:[]
    },
    dataList:{
      type:Array,
      default:[]
    },
    cptType:{
      type:Number,
      default:0
    },
    fiveAreas:{
      type:Object,
      default:{}
    }
  },
  data() {
    return {
      type:0,
      histogramColor:['#F86864','#F86864','#538F5A','#343434'],
      isBlock:false
    }
  },
  watch:{
    dataList:{
      deep:true,
      immediate:true,
      handler(res){
        
        if(res.length == 0) return;
        
        let actions = {
          0:()=>{
            let xName = this.type == 0?'人数':'百分比'
            this.$myecharts.top10Map('topTen',res,this.histogramColor[this.type],xName)
          },
          1:()=>{
  
            if(this.type !== 1){
              let xName = ['严重指数','','每百万','每平方公里']
               this.$myecharts.top10Map('topSerious',res,'#8A121C',xName[this.type])
            }
          },
        }
        actions[this.cptType].call(this)
      }
    },
    fiveAreas:{
      deep:true,
      immediate:true,
      handler(res){
        if(JSON.stringify(res) === '{}') return;
        if(this.cptType === 1 && this.type === 1){
          this.$myecharts.mainlandMap("five-region",res.fiveAreas,res.date,true,'严重指数')
        }
      }
    }
  },
  methods: {
    checkType(e){
      this.type = e;
      this.$emit('checkTbas',e);
    }
  },
}
</script>
<style lang="less" scoped>
.main{
    .main-title-topTen{
      background-color:#FFF;

      #topTen,#topSerious,#five-region{
        width:714px;
        height:550px;
      }
    }
  

  .tbas{
    margin-top:30px;
    p{
      background: #fff;
      text-align: center;
      font-size: 24px;
      line-height: 40px;
      border-radius: 10px;
      position: relative;
      padding: 12px 16px;
      color: #808595;
      margin: 0 10px;
      flex: 1;
      border: 2px solid #D7DEEB;
      position: relative;
    }
    .active{
      background: #E0E8FF;
      color: #4D79F3!important;
      border-color: #BDCFFF;
    }
    .active::after{
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      border: 2px solid #BDCFFF;
      border-width: 0 0 2px 2px;
      display: block;
      top: -10px;
      left: 50%;
      margin-left: -8px;
      background: #E0E8FF;
      transform: rotate(135deg);
      width: 10px;
      height: 10px;
    }
  }
}
</style>