        <span>{{ item.name }}</span>
        <span v-if="item.isclick" class="isicon" :style="'color:' + colorArr[item.type]">
          <i class="iconfont icon-xiala2 icon-rotate"></i>
          <i class="iconfont icon-xiala2 icon-lower"></i>
        </span>
      </p>
    </div>
  </div>
</template>

<script>
export default {
  props:['titles','colorArr'],
  data() {
    return {

    }
  },
  methods: {
    listsort(e,isclick){
      this.$emit('listsortType',{type:e,isclick})
    }
  },
}
</script>
<style lang="less" scoped>
  .main{
    .titles {
    background-color: #eaeaea;
    color: #000;
    font-size: 22px;

    p {
      padding: 0 10px;
      flex: 1 1 auto;
      height: 40px;
      line-height: 40px;

      .isicon {
        i {
          font-size: 4px !important;
        }

        position: relative;
        margin-top:5px;

        .icon-rotate {
          transform: rotate(180deg);
          position: absolute;
          bottom: 10px;
        }
      }
    }
  }
  }
</style>