import echarts from "echarts";
const worldEcharts = function (Vue) {
  Object.defineProperties(Vue.prototype, {
    $worldecharts: {
      get() {
        return {
          countryMap(id, cityName, data) {
            
            let countryName = require(`@/utils/map/worldMap/json/${cityName}.json`);
            echarts.registerMap(cityName, countryName, cityName === 'USA' ? {
              Alaska: {
                left: -131,
                top: 25,
                width: 15
              },
              Hawaii: {
                left: -110,
                top: 28,
                width: 5
              },
              'Puerto Rico': {
                left: -76,
                top: 26,
                width: 2
              }
            }:{});
            
            let myEcharts = echarts.init(document.getElementById(id));
            let option = {
              title: {
                x: "center",
                textStyle: {
                  fontSize: 14
                }
              },
              grid:{
                top :'middle',
                height:'auto',
                width:'auto',
                
              },
              tooltip: {
                trigger: "item",
                backgroundColor: "rgba(0,0,0,0.8)",
                textStyle:{
                  fontSize:10
                },
                formatter:function(e){
                  let {cityName,value,cureNum,deathNum} = e.data
                  return `
                    <div style="padding:5px;">
                      <p>${cityName}<p/>
                      <p>确诊：${value}<p/>
                      <p>治愈：${cureNum}<p/>
                      <p>死亡：${deathNum}<p/>
                    <div/>
                  `
                }
              },
              feature: {
                dataView: { readOnly: false },
                restore: {},
                saveAsImage: {}
              },
              visualMap: {
                orient: "horizontal",
                type: "piecewise",
                pieces: [
                  { min: 0, max: 0, color: "#fff" },
                  { min: 1, max: 9, color: "#FCEED3" },
                  { min: 10, max: 99, color: "#F0A98B" },
                  { min: 100, max: 499, color: "#E26553" },
                  { min: 500, max: 999, color: "#C82E30" },
                  { min: 1000, max: 9999, color: "#76161A" },
                  { gt: 10000, color: "#4F080E" }
                ],
                textStyle: {
                  fontSize: 8,
                  color: "#444444"
                },
                left: "center",
                itemWidth: 8,
                itemHeight: 8,
                align: "left",
                padding: 0,
                maxOpen: true,
                top: "auto",
                textGap: 6,
                itemGap: 6
              },
              series: [
                {
                  type: 'map',
                  roam: true,
                  map:cityName,
                  aspectScale:0.75,
                  layoutCenter: ["5%", "5%"],
                  label: {
                    show: false,
                    textStyle: {
                      fontSize: 8
                    },
                    formatter:function(e){
                      return e.data && e.data.cityName
                    }
                  },
                  roam: false,
                  zoom:1.1,
                  itemStyle: {
                    
                    normal: {
                      areaColor: "rgba(0,255,236,0)",
                      borderColor:'#8B7F7F',
                    },
                    emphasis: {
                      areaColor: "#FFFB85",
                    }
                  },
                  
                  
                  data
                }
              ]
            };
            myEcharts.setOption(option,true);
          },

        }
      }
    }
  })
};
export default worldEcharts;