<template>
  <div class="wrap">
    <div class="title-chart">
      <Title :title="name +'疫情趋势图'" />
      <p class="daysNum">
        <span :class="diffNum === 0 ? 'active':''" @click="onpick(0)">30天</span> |
        <span :class="diffNum === 1 ? 'active':''" @click="onpick(1)">全部</span>
      </p>
    </div>
    <div id="trend"></div>
  </div>
</template>

<script>
import { serverity } from '@/api/index';
import Title from '@/components/title';
export default {
  props: ['name'],
  data() {
    return {
      diffNum: 0,
      serverityData: [],
      cityCode:'beijing',
    }
  },
  components: {
    Title
  },
  mounted() {
    this.onServerity('beijing');
    this.$nextTick(function () {
      this.$on('childmethods', function (e) {
        this.cityCode = e;
        this.onServerity(e);
      });
    })
  },
  methods: {
    
    // 选择城市
    async onServerity(cityCode) {
      let list = []
      let serverityData = await serverity(cityCode);
      if (this.diffNum === 0) {

        list = serverityData.data.historylist.slice(0, 30);
        list.sort((a, b) => a.ymd > b.ymd ? 1 : -1);
      } else {
        list = serverityData.data.historylist;
        list.sort((a, b) => a.ymd > b.ymd ? 1 : -1);
      }
      // 无症状患者
      let Asymptomatic = list.map(item => item.locAsymNum)
      // 确诊病例
      let diagnosisCases = list.map(item => item.conadd)
      // 死亡病例
      let deathCases = list.map(item => item.deathadd)
      // 治愈病例
      let cureCases = list.map(item => item.cureadd)
      // 日期
      let date = list.map(item => item.date)

      this.$myecharts.trendMap(
        'trend',
        date,
        Asymptomatic,
        diagnosisCases,
        deathCases,
        cureCases
      );
    },
    onpick(e) {
      this.diffNum = e
      this.onServerity(this.cityCode);
    },

  },
}
</script>
<style lang="less" scoped>
.wrap {
  width: 714px;
  margin: 0 auto;

  .title-chart {
    display: flex;
    justify-content: space-between;
    background-color: #FFF;
    height: 80px;
    line-height: 80px;

    .pr_name {
      font-size: 32px;
      font-weight: bold;
      color: #4d79f3;

    }

    .daysNum {
      display: flex;
      font-size: 20px;
      color: #ccc;
      height: 60px;
      margin-top: 10px;
      text-align: center;
      flex-direction: row;
      align-items: center;
      justify-content: space-around;
      width: 200px;
      border: 1px solid rgba(204, 204, 204, 0.6);
      border-radius: 30px;
      margin-left: auto;
      margin-right: 10px;

      span {
        font-size: 26px;
        color: #444;
      }

      span.active {
        color: #3192F1;
      }
    }
  }

  #trend {
    width: 714px;
    height: 650px;
    background-color: #FFF;
  }
}
</style>