<template>
  <div class="list">
    <div class="titlebox d-flex d-cont-between align-item">
      <Title :title="'近期31省区市本土病例'" />
      <router-link class="btn_risk" to="/riskLevel">风险地区</router-link>
    </div>
    <table class="epidemic_analysis_table">
      <thead>
        <tr>
          <th>城市(区)</th>
          <th>省区市</th>
          <th>新增</th>
          <th>现有确诊</th>
          <th>详细情况</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in list" :key="index">
          <td>{{ item.name }}</td>
          <td>{{ item.province }}</td>
          <td :class="item.today.local_confirm_add ? 'ft_c_FE350E' : ''">
            {{ item.today.local_confirm_add ? '+' : '' }}{{ item.today.local_confirm_add }}</td>
          <td>{{ item.total.nowConfirm }}</td>
          <td class="more">
            
            <router-link :to="{name: 'CovDomestic', params: { citycode: item.pinyin, diff:0}}">查看更多</router-link>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="openMore" @click="pageList(num++)">
      <span>展开更多</span>
      <i class="iconfont icon-xiala"></i>
    </div>
  </div>
</template>

<script>
import { covid } from '@/api/indexCovid';
import { pinyin } from 'pinyin-pro';
/** 将数组进行分页，返回新的分页数组
 * @param {Object} pageSize 每页大小
 * @param {Object} arr 数组
 */
import Title from '@/components/title';
export default {
  data() {
    return {
      city: [],
      list: [],
      num: 0
    }
  },
  //生命周期 - 挂载完成（访问DOM元素）
  mounted() {
    
    this.covidContent();
  },
  components: {
    Title
  },
  methods: {
    async covidContent() {
      let Coviddata = await covid();
      let { diseaseh5Shelf } = Coviddata.data;
      let { children } = diseaseh5Shelf.areaTree[0];
      let city = [];
      children.forEach((item) => {
        item.adcode && item.children.forEach((cityItem) => {
          this.$set(cityItem, 'province', item.name);
          this.$set(cityItem, 'pinyin', pinyin(item.name,{ toneType: 'none' }).replace(/\s*/g,""));
          cityItem.adcode && city.push(cityItem);
        })
      });
      let cityData = city.sort((a, b) => {
        return (b.today.local_confirm_add - a.today.local_confirm_add)
      });

      this.returnAllPageFunc(40, cityData);
      this.pageList()
    },
    returnAllPageFunc(pageSize, arr) {
      let pageNum = 1;
      let pageObj = {
        pageNum: 1,
        list: []
      };
      let pageResult = [];
      let newArr = JSON.parse(JSON.stringify(arr));
      let totalPage = newArr.length ? Math.ceil(arr.length / pageSize) : 0  //计算总页数
      for (let i = 0; i <= totalPage; i++) {
        if (totalPage == 1) {
          pageNum += 1
          pageObj.list = newArr.splice(0, arr.length)
        } else if (i <= totalPage) {
          pageNum += 1;
          pageObj.list = newArr.splice(0, pageSize)
        } else {
          pageObj.list = newArr.splice(0, arr.length % pageSize)
        }
        pageResult.push(pageObj);
        pageObj = {
          pageNum: pageNum,
          list: []
        }
      }
      this.city = pageResult;
    },
    pageList() {
      if (this.num !== 12) {
        this.city[this.num].list.forEach((item) => {
          this.list.push(item)
        })
      }

    },
  },
}
</script>
<style lang="less" scoped>
.list {
  background-color: #FFF;
  width: 714px;
  margin: 0 auto;
  border-radius: 10px;
  box-shadow: 0 1px 12px 0 rgb(0 0 0 / 5%);
  margin-top: 30px;

  .titlebox {
    .btn_risk {
      height: 48px;
      border: 2px solid #4D79F3;
      border-radius: 24px;
      line-height: 48px;
      font-size: 26px;
      color: #4D79F3;
      margin: 0 10px 0 auto;
      padding: 0 14px;
      cursor: none;
      background: #fff;

    }
  }

  .epidemic_analysis_table {
    width: 100%;
    text-align: center;

    thead {
      display: table-header-group;
      font-size: 24px;
      color: #4D79F3;

      tr {
        th {
          display: table-cell;
          font-weight: normal;
          height: 50px;
          text-align: center;
          background-color: #EDF1FE;
          border-right: 1px solid #fff;
        }
      }
    }

    tbody {
      tr {
        td {
          font-size: 24px;
          height: 65px;
          color: #444444;
        }

        td.ft_c_FE350E {
          color: #FE350E;
        }
      }

      tr:nth-child(even) {
        background-color: #F8F8F8;
      }
    }
  }

  .openMore {
    text-align: center;
    height: 26px;
    height: 77px;
    line-height: 77px;
    color: #4D79F3;
  }
}
</style>