<template>
    <div class="topMapData2">
        <Title :title="'全国(含港澳台)'" />
        <div class="covidlist">
            <div class="li">
                <h4>现存确诊</h4>
                <p style="color: rgb(164,130,86);">{{ covidData.currentConfirmedCount }}</p>
            </div>
            <div class="li">
                <h4>现存无症状</h4>
                <p style="color: rgb(201,97,97);">{{ covidData.seriousCount }}</p>
            </div>
            <div class="li">
                <h4>累计确诊</h4>
                <p style="color: rgb(255,162,0);">{{ covidData.confirmedCount }}</p>
            </div>
            <div class="li">
                <h4>累计境外输入</h4>
                <p style="color: rgb(138,56,161);">{{ covidData.suspectedCount }}</p>
                <span>较昨日 + <i>{{covidData.yesterdaySuspectedCountIncr}}</i></span>
            </div>
            <div class="li">
                <h4>累计治愈</h4>
                <p style="color: rgb(184,84,14);">{{ covidData.curedCount }}</p>
                <span>较昨日 + <i>{{covidData.curedIncr}}</i></span>
            </div>
            <div class="li">
                <h4>累计死亡</h4>
                <p style="color: rgb(255,53,53);">{{ covidData.deadCount }}</p>
                <span>较昨日 + <i>{{covidData.deadIncr}}</i></span>
            </div>
        </div>
        <div class="splitLine">
        
    </div>
    </div>

</template>

<script>
import { covidData } from '@/api/indexCovid';
import Title from '@/components//title';
export default {
    name: "diagnosis",
    data() {
        return {
            covidData: []
        }
    },
    components:{
        Title
    },
    created() {
        this.handleData();
    },
    methods: {
        async handleData() {
            let data = await covidData();
            this.covidData = data.newslist[0].desc;
        }
    }
}
</script>
