    margin-top: 20px;
    background-color: #FFF;
    border-radius: 4px;
    .splitLine{
        font-size: 0;
        line-height: 0;
        margin: 0 20px;
        background-color: #EFF0F9;
        height: 2px;
    }
    .covidlist {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;

        .li {
            width: 33.33333%;
            text-align: center;
            margin-bottom: 32px;
            h4 {
                font-size: 20px;
                font-weight: normal;
            }

            p {
                font-size: 42px;
                font-weight: bold;
            }
            span{
                color: #999;
