// 数据来自挖数据 返回城市疫情风险地区
export const risklevel = (region_name) => {
  let rules = md5('appid20613city_name'+ region_name +'formatjson1f309e0bf02e834ba1f390b48e6155a3');
  let data = 'format=json&appid=20613&city_name='+ region_name +'&sign=' + rules;

  return http.get("https://uaqy.api.storeapi.net/api/94/221?" + data)
};

