<template>
  <div class="city">
    <Title :title="name + '' + dataChanges + '确诊疫情地图'" />
    <div id="cityMain"></div>
    <div class="d-flex d-cont-between btns align-item">
      <button :class="active == 0 ? 'active' : ''" @click="coviddata(0)">现存数据</button>
      <button :class="active == 1 ? 'active' : ''" @click="coviddata(1)">累计数据</button>
    </div>
  </div>
</template>

<script>
import { fourepidemic } from '@/api/indexCovid'
import Title from '@/components/title';
export default {

  data() {
    return {
      active: 0,
      mapdata: [],
      name: '北京',
      dataChanges: '现存',
      citycode: 'beijing'
    }
  },
  components: {
    Title
  },
  watch: {
    active(newVal) {
      let cityMap = this.mapdata.map(item => {
        return { name: item.mapName, value: newVal == 0 ? item.econNum : item.conNum }
      })
      this.$myecharts.cityMap('cityMain', this.name, this.citycode, cityMap)
      this.dataChanges = newVal == 0 ? '现存' : '累计';
    }
  },
  mounted() {
    this.fourCityData('北京', 'beijing');
    this.$nextTick(() => this.$on('onCovidCity', (name, e) => {
      this.name = name;
      this.citycode = e;
      this.fourCityData(name, e)
    }))
  },
  methods: {
    async fourCityData(name, cityCode) {
      let fourCityData = await fourepidemic(cityCode);
      let mapdata = fourCityData.data.city.filter(item => item.mapName !== '');
      this.mapdata = mapdata;
      let cityMap = mapdata.map(item => {
        return { name: item.mapName, value: parseInt(item.econNum) }
      })
      this.$myecharts.cityMap('cityMain', name, cityCode, cityMap)
    },
    coviddata(val) {
      this.active = val;
    },
  },

  beforeDestroy() {
    this.$bus.$off('cityData')
  },
}
</script>
<style lang="less" scoped>
.city {
  width: 714px;
  background-color: #FFF;
  margin: 0 auto;
  margin-top: 20px;

  #cityMain {
    width: 714px;
    height: 760px;
  }

  .btns {
    padding: 30px 20px 0 20px;

    button {
      background: #fff;
      text-align: center;
      font-size: 24px;
      line-height: normal;
      border-radius: 10px;
      position: relative;
      color: #9A9EAD;
      height: 60px;
      flex: 1;
      border: 2px solid #D7DEEB;
      margin-right: 16px;
    }

    .active {
      background: #f1f4ff;
      color: #4D79F3 !important;
      border-color: #BDCFFF;
      font-weight: bold;
    }

    .active::after {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      border: 1px solid #BDCFFF;
      border-width: 0 0 1px 1px;
      display: block;
      top: -8px;
      left: 50%;
      margin-left: -3px;
      background: #f1f4ff;
      transform: rotate(135deg);
      width: 10px;
      height: 10px;
    }
  }
}
</style>