<template>
  <div class="wrap">
    <div class="tab-content-nav-link d-flex d-cont-end align-item">
      <van-popover v-model="showPopover" @select="ondays" trigger="click" :actions="actions">
        <template #reference>
          <button>{{actions[daysType].text}} <i class="iconfont icon-xiala2"></i></button>
        </template>
      </van-popover>
      <van-popover v-model="showPopover2" @select="onexisting" trigger="click" :actions="actions2">
        <template #reference>
          <button>{{actions2[pointType].text}} <i class="iconfont icon-xiala2"></i></button>
        </template>
      </van-popover>
    </div>
  </div>
</template>

<script>
export default {
  props:{
    pointNum:{
      type:Number,
      default:0
    },
    daysNum:{
      type:Number,
      default:0
    }
  },
  data() {
    return {
      showPopover: false,
      showPopover2: false,
      pointType:this.pointNum,
      daysType:this.daysNum,
      days: '30天',
      existing: '现存',
      actions: [{ text: '30天', type: 0 }, { text: '年内', type: 1 }, { text: '全部', type: 2 }],
      actions2: [{ text: '现存', type: 0 }, { text: '新增', type: 1 }, { text: '累计', type: 2 }],
    }
  },
  methods: {
    ondays(e) {
      this.$emit('updatedays',e);
      this.daysType = e.type;
    },
    onexisting(e) {
      this.$emit('updateexsting',e);
      this.pointType = e.type;
    }
  },
}
</script>
<style lang="less" scoped>
.wrap {
  margin-bottom:20px;
  .tab-content-nav-link {
    button {
      border: 2px solid rgba(204, 204, 204, 0.6);
      border-radius: 24px;
      padding: 0 20px;
      height: 48px;
      line-height: 48px;
      position: relative;
      font-size: 26px;
      color: #444444;
      margin-right: 20px;
      background-color: #fff;
    }
  }
}
</style>