<template>
  <div class="wrap">
    <div class="pick">
      <span>城市选择:</span>
      <van-popover v-model="showPopover" placement="bottom-end" trigger="click">
        <van-grid column-num="3" style="width: 240px;">
          <van-grid-item v-for="(i,index) in actions" :key="index" :text="i.name" @click="onSelect(i.cityCode,i.name)" />
        </van-grid>
        <template #reference>
          <p class="pick-city">
            <span>{{name}}</span>
            <i class="iconfont icon-xiala2"></i>
          </p>
        </template>
      </van-popover>
    </div>
    <TrendChart ref="trendcharts" :name="name"/>
    <city ref="covidCity" :name="name"/>
  </div>
</template>

<script>
import { fourCity } from '@/api/index';
import TrendChart from './city/TrendChart.vue';
import city from './city/city.vue';
export default {
  data() {
    return {
      cityCode:'beijing',
      name: '北京',
      showPopover: false,
      actions: [],
    }
  },
  mounted() {
    this.onFourCity();
  },
  components:{
    TrendChart,
    city
  },
  methods: {
    async onFourCity() {
      let cityData = await fourCity();
      this.actions = cityData.data;
    },
    onSelect(e, name) {
      this.name = name;
      this.showPopover = false;
      this.cityCode = e;
      this.$refs.trendcharts.$emit('childmethods',e)
      this.$refs.covidCity.$emit('onCovidCity',name,e)
    }
  },
}
</script>
<style lang="less" scoped>
  .pick {
    display: flex;
    justify-content: space-between;
    height: 70px;
    margin: 30px 0;
    line-height: 70px;
    padding: 0 20px;
    font-size: 30px;

    .pick-city {
      font-weight: bold;
      color: #4D79F3;
    }
  }
</style>