
import Vue from 'vue';
Vue.filter('amount', (amount) => {
  return parseInt(amount / 10000);
});
// 获取给定时间戳的时间
Vue.filter('getTimestamp', (timestamp) => {
  let date = new Date(timestamp);
    let y = date.getFullYear();
    let m = date.getMonth() + 1;
    m = m < 10 ? ('0' + m) : m;
    let d = date.getDate();
    d = d < 10 ? ('0' + d) : d;
    let h = date.getHours();
    h = h < 10 ? ('0' + h) : h;
    let minute = date.getMinutes();
    let second = date.getSeconds();
    minute = minute < 10 ? ('0' + minute) : minute;
    second = second < 10 ? ('0' + second) : second;
    return h+':'+minute;
});
Vue.filter('deletewords', (words) => {
  let delWords = '';
  if(words.indexOf('更多最新实时数据 上新浪新闻客户端') !=-1){
    delWords = '更多最新实时数据 上新浪新闻客户端';
  }else if(words.indexOf('上新浪新闻客户端 查看最新实时数据') !=-1){
    delWords = '上新浪新闻客户端 查看最新实时数据';
  }else if(words.indexOf('下载新浪新闻客户端 查看最新实时数据') !=-1){
    delWords = '下载新浪新闻客户端 查看最新实时数据';
  }else if(words.indexOf('查看最新实时数据 请前往新浪新闻客户端') !=-1){
    delWords = '查看最新实时数据 请前往新浪新闻客户端';
  }else if(words.indexOf('请前往新浪新闻客户端 查看最新实时数据') !=-1){
    delWords = '请前往新浪新闻客户端 查看最新实时数据';
  }
    return words.replace(delWords ,'');
});