
<template>
  <div class="main">
    <Title :title="'重点疫情国家累计确诊趋势'" />
    <div id="overseas"></div>
    <div class="tbas">
        <ul class="d-flex d-cont-between">
            <li 
            v-for="(item ,index) in list" 
            :key="index" :style="'border:1px solid' + colorArr[index]" class="d-flex d-cont-end align-item"
            
            >
              <router-link :to="`/covAbroad/${item.citycode}`">
                <span class="line" :style="'background:' + colorArr[index]"></span>
              <i :style="'background:' + colorArr[index]"></i>
              <span :style="'color:' + colorArr[index]">{{item.name}}</span>
              </router-link>
            </li>
        </ul>
    </div>
  </div>
</template>

<script>
import Title from '@/components/title';
import {keyCountries} from '@/api/index';
export default {
  data() {
    return {
      list:[],
      colorArr:['#CB2A0B','#0D9074','#CC5B04','#2774C0','#50A100','#CC0065','#7F27C0','#515151'],
    }
  },
  created() {

  },
  components:{
    Title
  },
  mounted() {
    this.getCountries();
  },
  methods: {
    async getCountries(){
      let overseasARr = [];
      let Countriesdata = await keyCountries();
      let result = Countriesdata.result.data.multi_historylist;
      result.forEach((item,index) =>{
        item.historylist.sort((a,b) => a.conNum - b.conNum);
        overseasARr.push({
            name:item.name,
            data:item.historylist.map(items=>items.conNum),
            type: 'line',
            color:this.colorArr[index],
            smooth:0.1,
            symbolSize: 6,
            symbol:'circle',
        })
      })
      let date = result[0].historylist.map(items => items.date.substring(5,items.date.length));
      this.$myecharts.mainlandMap("overseas",overseasARr,date,false,'人数')
      this.list = result;
    }
  },
}
</script>
<style lang="less" scoped>
  .main{
    margin:0 auto;
    width:714px;
    background-color:#FFF;
    #overseas{
      width:714px;
      height:714px;
    }
    .tbas{ 
      padding:0 20px;
      ul{
        flex-wrap: wrap;
        li{
          width:150px;
          position: relative;
          height:50px;
          margin-bottom:20px;
          border-radius:100px;
          padding:0 10px;
          .line{
            width: 20px;
            height: 2px;
            display: block;
            position: absolute;
            top:24px;
            left:30px;
          }
          i{
              display:block;
              position:absolute;
              width:10px;
              height:10px;
              top:20px;
              border-radius:50%;
              left:35px;
            }
        }
      }
    }
  }
</style>