<template>
  <div class="main main-center"> 
    <div class="title-top d-flex d-cont-start align-item">
      <Title  :title="'境外病例'"/>
      <span>共200个国家和地区发现确诊病例</span>
    </div>
    <div class="titles-box">
      <Titles :titles="titles" :colorArr="colorArr" v-on:listsortType="sortType"/>
    </div>
    <div class="list">
       <div v-for="item in topten" :v-key="item.citycode" class="sequence d-flex d-cont-between align-item">
        <p class="c1 line1">{{item.name}}</p>
        <p class="c2">{{item.conadd}}</p>
        <p class="c3">{{item.conNum}}</p>
        <p class="c4">{{item.deathNum}}</p>
        <p class="c5">{{item.cureNum}}</p>
        <p class="c6">
          <router-link class="link-c6" v-if="item.is_show_entrance == 1" :to="`/covAbroad/${item.citycode}`">更多</router-link>
        </p>
        
       </div>
    </div>
  </div>
</template>

<script>
import {foreign} from '@/api/index';
import Title from '@/components/title';
import Titles from '@/components/titles';
export default {
  data() {
    return {
      colorArr:['','#FF9D99','#E07979','#AAAAAA','#7BD6C1',''],
      titles: [
        { name: '国家', type: 0, bgColor: '#eaeaea' ,isclick: false,loading:false},
        { name: '新增', type: 1, diff: 0, bgColor: '#FFE2DC', isclick: true ,loading:false},
        { name: '累计', type: 2, diff: 0, bgColor: '#FFC9C9', isclick: true ,loading:false},
        { name: '死亡', type: 3, diff: 0, bgColor: '#eaeaea', isclick: true ,loading:false},
        { name: '治愈', type: 4, diff: 0, bgColor: '#C0ECE0', isclick: true ,loading:false},
        { name: '详情', type: 5, bgColor: '#eaeaea' ,isclick: false,loading:false},
      ],
      type:2,
      list:[],
      topten:[]
    }
  },
  components:{
    Title,
    Titles,
  },
  watch:{
    titles:{
      immediate:true,
      deep:true,
      handler(newval){
        let list = [];
        
        let actions = {
          '1':()=>{
            newval[this.type].diff === 1 ? list = this.list.sort((a,b)=>b.conadd - a.conadd) :list = this.list.sort((a,b)=>a.conadd - b.conadd)
          },
          '2':()=>{
            newval[this.type].diff === 1 ? list = this.list.sort((a,b)=>a.conNum - b.conNum) :list = this.list.sort((a,b)=>b.conNum - a.conNum)
          },
          '3':()=>{
            newval[this.type].diff === 1 ?list = this.list.sort((a,b)=>b.deathNum - a.deathNum) :list = this.list.sort((a,b)=>a.deathNum - b.deathNum)
          },
          '4':()=>{
            newval[this.type].diff === 1 ? list= this.list.sort((a,b)=>b.cureNum - a.cureNum) : list =this.list.sort((a,b)=>a.cureNum - b.cureNum)
          }
        }
        actions[this.type].call(this);
        this.topten  = list.slice(0,10);
      }
    }
  },
  mounted() {
    this.getForeign();
  },
  methods: {
    async getForeign(){
      let res = await foreign();
      res.result.data.sort((a,b) =>b.conadd - a.conadd)
      this.list = res.result.data;
      this.titles.forEach(item => item.loading = true);
    },
    sortType(e){
      if(!e.isclick) return;

      this.titles.forEach(item =>{
        if(e.type === item.type){ 
          item.diff === 0 || item.diff===2 ? item.diff = 1: (item.diff === 1 ?item.diff = 2:1 )
        }
      })
      this.type = e.type;
    }
  },
}
</script>
<style lang="less" scoped>
.main{
  background-color:#FFF;
  margin-bottom:30px;
  .title-top{
    margin-top:30px;
    span{
        margin-left:10px;
        color: #777;
        line-height:1;
      }
  }
  .list{
   .sequence{
    padding: 0 10px;
    height:80px;
    p{
      flex: 1;
      text-align: center;
      font-size:26px;
    }
    .c1{
      flex:none;
      width:12%;
      color:#4d79f3;
      text-align: left;
    }
    .c6{
      width:12%;
      .link-c6{
        
      text-decoration: none;
      color: #4d79f3;
      }
    }
   }
   .sequence:nth-child(odd){
        background-color: #F9FAF9;
      }
  }
}
</style>