import Vue from 'vue'
import App from './App.vue'
import './assets/css/basic.css'
import './assets/css/iconfont.css'
import 'vant/lib/index.css'

// 按需引入vant组件
import vant from '@/utils/vant'
import router from './router/router.js'
// 引入echarts插件
import echarts from '@/plugin/echarts.js'
// 引入世界国家echarts插件
import worldEcharts from '@/plugin/worldEcharts.js'
// 引入百度地图
import BaiduMap from 'vue-baidu-map'
// 引入高德地图
import VueAMap from 'vue-amap';


Vue.config.productionTip = false;
Vue.use(VueAMap);
Vue.use(echarts);
Vue.use(worldEcharts);
// 高德地图
VueAMap.initAMapApiLoader({
  key: '7ddf78098fa813e0fce627b9fd2a6732',
  plugin: ['Autocomplete', 'PlaceSearch', 'Scale', 'OverView', 'ToolBar', 'MapType', 'PolyEditor', 'AMap.CircleEditor','MarkerClusterer','ControlBar','Geolocation'],
  v: '1.4.4',
  uiVersion: '1.0.11'
});
// 百度地图
Vue.use(BaiduMap, {
  ak: 'n1nG11K9cWycnM6DNhhy7V9jfbFsmpaZ'
})
new Vue({
  router,
  render: h => h(App),
  beforeCreate() {
    Vue.prototype.$bus = this//安装全局事件总线
  }
}).$mount('#app')
