<template>
  <div class="wrap">
    <Title class="title main-center" v-slot:title :title="titleArr[type]" />
    <trendchart 
      :topTenTabs="topTenTabs"
      :dataList="dataList"
      v-on:checkTbas="onCheckTbas"
      :cptType="1"
      :fiveAreas="fiveSeriousAreas"
    />
  </div>
</template>

<script>
import { severityIndex, certain } from '@/api/index';
import trendchart from './trendchart';
import Title from '@/components/title.vue';
export default {
  data() {
    return {
      topTenTabs: [
        { type: 0, text: '海外各国严重指数' },
        { type: 1, text: '前5严重国家历史趋势' },
        { type: 2, text: '确诊/人口国家排行' },
        { type: 3, text: '确诊/面积国家排行' },
      ],
      titleArr:[
        '海外各国疫情严重指数TOP10',
        '前5严重国家历史趋势',
        '确诊/百万人口 TOP10国家',
        '确诊/建成区面积 TOP10国家',
      ],
      type: 0,
      dataList:[],
      fiveSeriousAreas:{}
    }
  },
  components: {
    trendchart,
    Title
  },
  mounted() {
    this.getSeverityIndex(0);
  },
  methods: {
    async getSeverityIndex(type) {
      let result1 = await severityIndex();
      let result2 = await certain();
      let dataList = [];
      let actions = {
        0:()=>{
          let yesterday = result1.result.yesterday_data.slice(0,10);
          yesterday.forEach(item => {
            dataList.push({
              name:item.name,
              value:item.serverity_index,
              citycode:false,
              title:'严重指数'
            });
          });
        },
        1:()=>{
          let fiveAreas = [];
          let colorArr = ['#000000','#74011A','#E60134','#FCA846','#FCE246']
          result1.result.data.forEach((item,index) =>{
            fiveAreas.push({
              name: item.name,
              data:item.serverity_index_data.map((item)=>item.serverity_index),
              type: 'line',
              color:colorArr[index],
              smooth: true,
              symbolSize: 6,
              symbol: "circle",
            })
          });
          let date = result1.result.data[0].serverity_index_data.map(items => items.date.substring(5,items.date.length))
          this.$set(this.fiveSeriousAreas,'fiveAreas',fiveAreas);
          this.$set(this.fiveSeriousAreas,'date',date);
        },
        2:()=>{

          result2.result.population.forEach(item => dataList.push({
            name:item.name,
            value:item.value,
            citycode:item.citycode,
            title:'每百万人口确诊人数'
          }))
        },
        3:()=>{
          result2.result.ucarea.forEach(item => dataList.push({
            name:item.name,
            value:item.value,
            citycode:item.citycode,
            title:'每平方公里确诊人数'
          }))
        },
      };
      actions[type].call(this);
      this.dataList = dataList;
    },
    onCheckTbas(e){
      this.type = e;
      this.getSeverityIndex(e)
    }
  },
}
</script>
<style lang="less" scoped>
  .wrap{
    margin-top:30px;
    .title{
    
    background-color:#FFF;
  }
  }
</style>