<template>
  <div class="wrap">
    <Title :title="'本土疫情现存趋势图'" />
    <div class="d-flex d-cont-between mt">
      
      <screen 
      v-on:updateexsting="onexsting" 
      v-on:updatedays="onupdatedays" 
      :pointNum="2" 
      :daysNum="0"/>
    </div>
    <div id="whole"></div>
  </div>
</template>

<script>
import Title from '@/components/title.vue';
import screen from './screen.vue';
import { duringYear ,extractValue} from './extract';
import { extract30 } from '@/utils/extract30';
export default {
  props: ['datalist'],
  data() {
    return {
      daysNum: 0,
      exisNum: 2,
      historylist:[]
    }
  },
  components: {
    Title,
    screen
  },
  created() {
    this.historylist = this.datalist
  },
  mounted() {
    this.getDataList(this.daysNum, this.exisNum);
  },
  methods: {    
    getDataList(daysNum, exisNum) {
      if (daysNum == 0) {   
        let sortlist = extract30(this.historylist,30).sort((a, b) => a.date > b.date ? 1 : -1);
        let mapdata = extractValue(sortlist, exisNum);
        let date = sortlist.map(item=>item.date)
        this.$myecharts.mainlandMap('whole', mapdata,date,true,'人数')
      }else if(daysNum == 1){
        let jsonhis = JSON.stringify(this.historylist);
        let jsonlist = JSON.parse(jsonhis);
        // 筛选年内的值
        let yearlist = duringYear(jsonlist).sort((a, b) => a.date > b.date ? 1 : -1);
        let mapdata = extractValue(yearlist, exisNum);
        let date = [];
        if(exisNum== 0){
          date = yearlist.map(item=>item.cn_econNum && item.date).filter(item=>item);
        }else if(exisNum== 1){
          date = yearlist.map((item)=>item.cn_conadd && item.date).filter(item=>item)
        }else if(exisNum == 2){
          date = yearlist.map(item=>item.date);
        }
        this.$myecharts.mainlandMap('whole', mapdata,date,true,'人数');
      }else if(daysNum == 2){
        let jsonhis = JSON.stringify(this.historylist);
        let jsonlist = JSON.parse(jsonhis);
        let sortlist = jsonlist.sort((a, b) => a.ymd > b.ymd ? 1 : -1);

        let mapdata = extractValue(sortlist, exisNum);
        let date = [];
        if(exisNum== 0){
          date = sortlist.map(item=>item.cn_econNum && item.date).filter(item=>item);
          
        }else if(exisNum== 1){
          date = sortlist.map((item)=>item.cn_conadd && item.date).filter(item=>item)
        }else if(exisNum == 2){
          date = sortlist.map(item=>item.date);
        }
        this.$myecharts.mainlandMap('whole', mapdata,date,true,'人数')
      }
    },
    onupdatedays(e){
      this.daysNum = e.type;
      this.getDataList(e.type, this.exisNum);
    },
    onexsting(e){
      this.exisNum = e.type;
      this.getDataList(this.daysNum, e.type);
    }
  },
}
</script>
<style lang="less" scoped>
.wrap {
  background-color: #FFF;
  width: 714px;
  margin: 0 auto;
  margin-top:30px;
  .mt{
    margin-bottom:30px;
  }
  #whole {
    width: 714px;
    height: 714px;
  }
}
</style>