import echarts from "echarts";
import "echarts/map/js/china";
const echartsAggregate = function (Vue) {
  Object.defineProperties(Vue.prototype, {
    $myecharts: {
      get() {
        return {
          chinaMap(id, data) {
            let myEcharts = echarts.init(document.getElementById(id));
            let option = {
              title: {
                x: "center",
                textStyle: {
                  fontSize: 14
                }
              },
              tooltip: {
                show:true,
                //这里设置提示框
                trigger: "item", //数据项图形触发
                backgroundColor: "rgba(0,0,0,0.7)", //提示框浮层的背景颜色。
                //字符串模板(地图): {a}（系列名称），{b}（区域名称），{c}（合并数值）,{d}（无）
                formatter: "地区：{b}<br/>数据：{c}"
              },
              visualMap: {
                //视觉映射组件
                orient: "horizontal",
                type: "piecewise",
                pieces: [
                  { min: 0, max: 0, color: "#fff" },
                  { min: 1, max: 9, color: "#FFF4EF" },
                  { min: 10, max: 99, color: "#FFE7DD" },
                  { min: 100, max: 499, color: "#FDB19B" },
                  { min: 500, max: 999, color: "#E7856D" },
                  { min: 1000, max: 9999, color: "#DE512F" },
                  { gt: 10000, color: "#970A0F" }
                ],
                textStyle: {
                  fontSize: 8,
                  color: "#444444"
                },
                left: "center",
                itemWidth: 8,
                itemHeight: 8,
                align: "left",
                padding: 0,
                maxOpen: true,
                top: "250",
                textGap: 6,
                itemGap: 6
              },
              series: [
                {
                  name: "省份",
                  type: "map",
                  mapType: "china",
                  aspectScale: 0.75,
                  layoutCenter: ["5%", "5%"],
                  label: {
                    show: true,
                    textStyle: {
                      fontSize: 8
                    }
                  },
                  roam: false, //是否开启鼠标缩放和平移漫游
                  itemStyle: {
                    //地图区域的多边形 图形样式
                    normal: {
                      //是图形在默认状态下的样式
                      areaColor: "rgba(0,255,236,0)",
                      borderColor: "rgba(0,0,0,0.2)"
                    },
                    zoom: 5, //地图缩放比例,默认为1
                    emphasis: {
                      //是图形在高亮状态下的样式,比如在鼠标悬浮或者图例联动高亮时
                      areaColor: "#FFFB85",
                     
                    }
                  },
                  top: "3%", //组件距离容器的距离
                  data
                }
              ]
            };
            // 使用刚指定的配置项和数据显示图表。
            myEcharts.setOption(option);
          },
          cityMap(id, cityName,citycode, data) {
            let countryName = require(`echarts/map/js/province/${citycode}`);
            echarts.registerMap(citycode, countryName)
            // 初始化echarts实例
            let myEcharts = echarts.init(document.getElementById(id));
            let option = {


              tooltip: {
                //这里设置提示框
                trigger: "item", //数据项图形触发
                backgroundColor: "rgba(0,0,0,0.7)", 
                //字符串模板(地图): {a}（系列名称），{b}（区域名称），{c}（合并数值）,{d}（无）
                formatter: "地区：{b}<br/>数据：{c}"
              },
              visualMap: {
                //视觉映射组件
                orient: "horizontal",
                type: "piecewise",
                pieces: [
                  { min: 0, max: 0, color: "#F8F8F8" },
                  { min: 1, max: 9, color: "#FEF4EE" },
                  { min: 10, max: 29, color: "#FCE5D9" },
                  { min: 30, max: 59, color: "#F8B18B" },
                  { min: 60, max: 99, color: "#E6846C" },
                  { min: 100, max: 499, color: "#DD502F" },
                  { gte: 500, color: "#97090F" }
                ],
                textStyle: {
                  fontSize: 8,
                  color: "#444444"
                },
                left: "center",
                itemWidth: 8,
                itemHeight: 8,
                align: "left",
                padding: 0,
                maxOpen: true,
                top: "auto",
                textGap: 6,
                itemGap: 6
              },
              series: [
                {
                  name: "城市",
                  type: "map",
                  mapType: cityName,
                  aspectScale: 0.8,
                 
                  label: {
                    show: true,
                    textStyle: {
                      fontSize: 8
                    },
                    offset: [0, 0],
                  },

                  zoom: 1.2,
                  roam: false,
                  itemStyle: {
                    normal: {
                      areaColor: "rgba(0,255,236,0)",
                      borderColor: "rgba(0,0,0,0.2)"
                    },
                    emphasis: {
                      areaColor: "#FFFB85",
                    }
                  },
                  top: "7%",
                  data
                }
              ]
            };
            // 使用刚指定的配置项和数据显示图表。
            myEcharts.setOption(option);
          },
          trendMap(id, date, Asymptomatic, diagnosisCases, deathCases, cureCases) {
            /**
             * @param date Array日期
             * @param Asymptomatic Array无症状患者
             * @param diagnosisCases  Array确诊病例
             * @param deathCases  Array死亡病例
             * @param cureCases Array治愈病例
             */
            var myTrendMap = echarts.init(document.getElementById(id));
            var option = {
              tooltip: {},
              legend: {
                itemWidth:10,
                itemHeight:8
              },
              tooltip:{
                trigger : 'axis',
                backgroundColor: '#FFF',
                borderColor:'#E9E9E9',
                borderWidth:'1',
                
                textStyle:{
                  color:'#000'
                },
                axisPointer:{
                  type:"line",
                  axis:'x',
                  snap:false,
                  lineStyle:{
                    color:'#C3C3C3',
                    width:'1',
                    type:'dashed'
                  },
                  
                }
              },
              xAxis: {
                show: true,
                nameLocation: 'end',
                offset: 0,
                data: date,
                axisLabel: {
                  interval:'auto',
                  textStyle: {
                    color: "#888888",
                  }
                },

                axisLine: {
                  show: false,
                  lineStyle: {
                    color: '#E4E4E4'
                  }
                },
              },
              yAxis: {
                offset: 0,
                show: true,    // 是否显示 y 轴
                type: 'value',
                name: '人数',
                nameTextStyle: {
                  color: "#969696",
                  fontSize: '12'
                },
                splitNumber: 4,
                data: [0, 10, 20, 30],
                axisPointer: {
                  type: 'line'
                },
                axisLine: {
                  lineStyle: {
                    color: '#E4E4E4',

                  }
                },
                axisLabel: {
                  interval: 0,
                  textStyle: {
                    color: "#888888",
                  }
                },
                splitArea: {
                  show: false,
                }
              },
              series: [
                {
                  name: '无症状',
                  data: Asymptomatic,
                  type: 'line',
                  color: '#8A38A1',
                  smooth: true,
                  symbolSize: 6,
                  symbol: "circle",
                },
                {
                  name: '确诊',
                  data: diagnosisCases,
                  type: 'line',
                  color: '#CF3313',
                  smooth: true,
                  symbolSize: 6,
                  symbol: "circle",
                },
                {
                  name: '死亡',
                  data: deathCases,
                  type: 'line',
                  color: '#2F2F2F',
                  smooth: true,
                  symbolSize: 6,
                  symbol: "circle",
                },
                {
                  name: '治愈',
                  data: cureCases,
                  type: 'line',
                  color: '#13B593',
                  smooth: true,
                  symbolSize: 6,
                  symbol: "circle",
                },
              ]
            };
            // 使用刚指定的配置项和数据显示图表。
            myTrendMap.setOption(option);
          },
          // 本土
          mainlandMap(id, mapdata, date, show,yName,inverseShow) {
            var myTrendMap = echarts.init(document.getElementById(id));
            var option = {
              tooltip:{
                trigger : 'axis',
                backgroundColor: '#FFF',
                borderColor:'#E9E9E9',
                borderWidth:'1',
                textStyle:{
                  color:'#000',
                  fontSize:10
                },
                axisPointer:{
                  type:"line",
                  axis:'x',
                  snap:false,
                  lineStyle:{
                    color:'#C3C3C3',
                    width:'1',
                    type:'dashed'
                  },
                  
                }
              },
              legend: {
                top:'7%',
                show: show,
                right: '30',
                itemWidth:10,
                itemHeight:8
              },
              grid: {
                show: true,
                left: '15%',
                right:'5%',
                borderColor: 'rgba(0,0,0,0)'
              },
              xAxis: {
                show: true,
                type: "category",
                nameLocation: 'end',
                offset: 0,
                splitNumber: 8,
                data: date,
                inverse:inverseShow?inverseShow:false,
                axisLabel: {
                  interval: 'auto',
                  showMinLabel: true,
                  showMaxLabel: true,
                  textStyle: {
                    color: "#888888",
                    fontSize: 10
                  }
                },
                axisLine: {
                  show: false,
                  lineStyle: {
                    color: '#E4E4E4'
                  }
                },
              },
              yAxis: {
                offset: 0,
                show: true,    // 是否显示 y 轴
                type: 'value',
                name:yName,
                nameGap: 0,
                nameTextStyle: {
                  color: "#969696",
                  fontSize: '12',
                  lineHeight: '30',
                },
                splitNumber: 4,
                data: [0, 1, 2, 3],
                axisPointer: {
                  type: 'line'
                },
                axisLine: {
                  lineStyle: {
                    color: '#E5E5E5',
                  },

                },
                axisTick: {
                  show: false,
                  inside: true,
                  length: 0,

                },
                axisLabel: {
                  interval: 0,
                  show: true,
                  fontSize: '10',
                  inside: false,
                  textStyle: {
                    color: "#888888",
                  }
                },
                splitArea: {
                  show: false,
                }
              },
              series: mapdata
            };
            myTrendMap.setOption(option, true);
          },
          top10Map(id, valueArr, color,xName) {

            let myChart = echarts.init(document.getElementById(id));
            /*指定图表的配置项和数据*/
            let option = {
              textStyle: {
                color: '#000',
                fontSize: '16'
              },

              tooltip: {
                trigger : 'item',
                triggerOn:"click",
                enterable:true,
                backgroundColor: '#FFF',
                borderColor:'#E9E9E9',
                borderWidth:'1',
                textStyle:{
                  color:'#000',
                  fontSize:10
                },
                axisPointer:{
                  type:'shadow'
                },
                formatter:function(item){
                  return `
                    <div>
                      <p><span> ${item.data.name}<span/><p/>
                      <p>${item.data.title}：${item.data.value}${item.data.percentage?item.data.percentage:''}<p/>
                      <a style="display:${item.data.citycode?'block':'none'};width:90px;margin:0 auto;text-align:center;border-radius:50px;line-height:20px;margin-top:10px;height:20px;background:#4B79F1;color:#FFF;" href="#/covAbroad/${item.data.citycode}">查看详细数据<a/>
                    <div/>
                  `
                }
              },
              grid: {//设置图表位置
                top:'0',
                right: '20%',
                left:'10',
                bottom:20,
                containLabel: true
              },
              xAxis: {
                color: '#000',
                splitLine: {//去掉网格线
                  show: true,
                  lineStyle:{
                    color:"#F1F1F1"
                  }
                },
                splitNumber:3,
                position: 'top',//X轴位置
                type: 'value',
                name:xName,
                nameTextStyle: {
                  fontSize: '10',
                  color: '#8C8C8C',
                },
                boundaryGap: [0, 0.01],
                axisLabel: {//坐标轴刻度标签的相关设置
                  show: true,
                  textStyle: {
                    color: '#989898',
                    align: 'right',
                    fontSize: 10
                  }
                },
                minorTick: {
                  show: false,
                  splitNumber: 4
                },
                axisLine: {
                  show:true,
                  lineStyle: {
                    color: '#CCCCCC'
                  }
                },
                axisTick: {
                  show: false,
                  lineStyle: {
                    color: '#000'
                  }
                },
              },
              yAxis: {
                show: false,
                type: 'category',//轴的类型分两种 1.category（类别）2.value(值)
                // data:nameArr,
                inverse: true,
                axisLabel: {
                  show: true,
                  textStyle: {
                    color: '#000',
                    align: 'right',
                    fontSize: 10    /*文字大小*/
                  }
                },
                axisLine: {
                  lineStyle: {
                    color: '#000'//轴的颜色
                  }
                },
                axisTick: {
                  lineStyle: {
                    color: '#000'//轴上点的颜色
                  }
                },

              },
              series: [
                {

                  type: 'bar',
                  data: valueArr,
                  legendHoverLink:true,
                  label: {
                    normal: {
                      show: true,
                      formatter: '{b}', //显示数据带上百分比
                      position: 'right' //控制数据显示位置，‘’right‘’为显示在柱状图右侧
                    }

                  },
                  barWidth: 8,
                  yAxisIndex: 0,
                  emphasis: {
                    label: {
                      show: true
                    },
                    itemStyle:{
                      opacity:0.5
                    }
                  },
                  itemStyle: {
                    normal: {
                      color: color,//柱状的颜色
                      label: {
                        textStyle: {
                          show: true,
                          fontSize: '10',//柱状上的显示的文字
                          color: '#000'
                        }
                      }
                    },

                  }
                }
              ]
            };
            myChart.setOption(option);
          }
        };
      }
    }
  });
};

export default echartsAggregate;
