
<template>
  <div class="wrap">
    <Title class="title main-center" v-slot:title :title="topTenTabs[type].text"/>
    <trendchart 
    
    :topTenTabs="topTenTabs" 
    :dataList="dataList"
    v-on:checkTbas="onCheckTbas"
    :cptType="0"
    :fiveAreas="{}"
    />
  </div>
</template>

<script>
import {topTenData ,daycertain ,recurep,diep} from '@/api/index';
import trendchart from './trendchart';
import Title from '@/components/title.vue';
export default {
  data() {
    return {
      topTenTabs:[
        {type:0,text:'海外昨日新增TOP10'},
        {type:1,text:'海外七日增幅TOP10'},
        {type:2,text:'海外治愈率TOP10'},
        {type:3,text:'海外死亡率TOP10'},
      ],
      tipsTitle:['昨日新增','七日增幅','治愈率','死亡率'],
      dataList:[],
      type:0
    }
  },
  components:{
    trendchart,
    Title
  },
  mounted() {
    this.topTen(0);
  },
  methods: {
    async topTen(type){
      let dataList = [];
      let res = [await topTenData(),await daycertain(),await recurep(),await diep()];

      res[type].result.data.multi_historylist.forEach(item => {
  
        dataList.push({ 
          name:item.name,
          value:type==0?item.sortvalue:(item.sortvalue * 100).toFixed(2),
          citycode:item.citycode,
          percentage:type !== 0? '%':'',
          title:this.tipsTitle[type]
        })
      });
      this.dataList = dataList;
    },
    onCheckTbas(e){
      this.type = e;
      this.topTen(e)
    }
  },
}
</script>
<style lang="less" scoped>
.wrap{
  margin-top:30px;
}
  .title{
    
    background-color:#FFF;
  }
</style>