<template>
  <div class="">
    <mainland v-if="datalist.length!=0" :datalist="datalist"/>
    <national v-if="datalist.length!=0" :datalist="datalist"/>
  </div>
</template>
 
<script>
import {situationdata} from '@/api/index';
import mainland from './cividsituation/mainland';
import national from './cividsituation/national';
export default {
  data() {
    return {
      datalist:[]
    }
  },
  components:{
    mainland,
    national
  },
  mounted() {
    
    this.getsituationdata(); 
  },
  methods: {
    async getsituationdata(){
      let getSituation = await situationdata();
      this.datalist = getSituation.data.historylist;

    }
  },
}
</script>
