// 截取前30条数据
let year = [];

// 筛选年内数据
export function duringYear(list){
  let myDate = new Date();
  let y = myDate.getFullYear();
  year = list.filter(item => item.ymd.indexOf(y) !==-1)
  return year;
}
// 本土疫情现存趋势图
export function takeValue(list,type) {
  let listData = [];
   if(type == 0){
     listData.push({
       name: '确诊',
       type: 'bar',
       data:list.map((item)=>item.cn_province_econNum),
       type: 'line',
       color: '#95610B',
       smooth: true,
       symbolSize: 6,
       symbol: "circle",
     },{
       name: '密接',
       type: 'bar',
       data:list.map((item)=>item.cn_contactNum ),
       type: 'line',
       color: '#E5743C',
       smooth: true,
       symbolSize: 6,
       symbol: "circle",
     },{
       name:'无症状',
       type: 'bar',
       color:'#A64CBF',
       data:list.map((item)=>item.cn_asymptomNum),
       type: 'line',
       smooth: true,
       symbolSize: 6,
       symbol: "circle",
     })
   }else if(type == 1){
     listData.push(
       {
         name: '确诊',
         type: 'bar',
         data:list.map((item)=>item.cn_local_conNum).filter(item=>item),
         type: 'line',
         color: '#95610B',
         smooth: true,
         symbolSize: 6,
         symbol: "circle",
       },
       {
         name:'无症状',
         type: 'bar',
         color:'#A64CBF',
         data:list.map((item)=>item.cn_add_asymNum).filter(item=>item),
         type: 'line',
         smooth: true,
         symbolSize: 6,
         symbol: "circle",
       }
       )
   }else if(type==2){
     listData.push(
        {
         name: '确诊',
         type: 'bar',
         data:list.map((item)=>item.cn_local_econNum).filter(item=>item),
         type: 'line',
         color: '#95610B',
         smooth: true,
         symbolSize: 6,
         symbol: "circle",
       },
     )
   };
 return listData;
}
export function extractValue(list,type) {
  let listData = [];
   if(type == 0){
    listData.push(
      {
       name: '确诊',
       type: 'bar',
       data:list.map((item)=>item.cn_econNum).filter(item=>item),
       type: 'line',
       color: '#95610B',
       smooth: true,
       symbolSize: 6,
       symbol: "circle",
     },
   )
   }else if(type == 1){
     listData.push(
       {
         name: '确诊',
         type: 'bar',
         data:list.map((item)=>item.cn_conadd).filter(item=>item),
         type: 'line',
         color: '#95610B',
         smooth: true,
         symbolSize: 6,
         symbol: "circle",
       },
       )
   }else if(type==2){
     listData.push({
      name: '确诊',
      type: 'bar',
      data:list.map((item)=>item.cn_conNum),
      type: 'line',
      color: '#95610B',
      smooth: true,
      symbolSize: 6,
      symbol: "circle",
    },{
      name: '治愈',
      type: 'bar',
      data:list.map((item)=>item.cn_cureNum ),
      type: 'line',
      color: '#13B593',
      smooth: true,
      symbolSize: 6,
      symbol: "circle",
    },{
      name:'无症状',
      type: 'bar',
      color:'#666666',
      data:list.map((item)=>item.cn_deathNum),
      type: 'line',
      smooth: true,
      symbolSize: 6,
      symbol: "circle",
    })
   };
 return listData;
}