<template>
  <div class="main main-center">
      <div class="title d-flex d-cont-start align-item">
        <Title :title="'海外'"/>
        <span>{{times}}数据统计</span>
      </div>
      <div class="tabs d-flex d-cont-arund">
        <p v-for="(cum ,index) in cumulative" :key="index" class="d-flex d-flex-column align-item">
          <span>{{cum.title}}</span>
          <span :style="'color:' + cum.color">{{index == 0 ?data.certain:(index == 1?data.recure:data.die) | amount}}万</span>
          <span>
            较昨日
            <i>+{{index == 0 ?data.certain_inc:(index == 1?data.recure_inc:data.die_inc) | amount}}万</i>
          </span>
        </p>
      </div>
      <div class="bottom-line"></div>    
  </div>
</template>

<script>
import { SinaData } from '@/api/indexCovid';
import '@/utils/filters';
import Title from '@/components/title';
export default {
  data() {
    return {
      data:[],
      times:'',
      cumulative:[{
        id:0,
        title:'累计确诊',
        color:'#B10000',
      },{
        id:1,
        title:'累计治愈',
        color:'#13B593',
      },{
        id:2,
        title:'累计死亡',
        color:'#666666',
      }],
    }
  },
  created() {

  },
  components:{
    Title
  },
  mounted() {
    this.getSinaData();
  },
  methods: {
    async getSinaData(){
      let res = await SinaData();
      this.times = res.data.times;

      this.data = res.data.othertotal;
    }
  },
}
</script>
<style lang="less" scoped>
  .main{
    background-color: #FFF;
    width:714px;
    margin-top:30px;
    .title{
      span{
        margin-left:10px;
        color: #777;
        line-height:1;
      }
    }
    .tabs{
      p{
        span:first-child{
          color: #393939;
          font-size: 20px;
          line-height:28px;
        }
        span:nth-child(2){
          font-size: 42px;
          font-weight: bold;
          line-height:60px;
        }
        span:last-child{
          color: #999;
          font-size: 20px;
          line-height:28px;
          i{
            font-weight:bold;
          }
        }
      }
    }
    .bottom-line{
      width:700px;
      height:2px;
      background-color:#EFF0F9;
      margin-top:30px;
     
    }
  }
</style>