import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/view/home/home.vue';
const NucleicAcid = ()=> import('@/view/nucleicAcid/index')
const Holdbox = ()=> import('@/view/holdbox/index');



// 疫情场所
const CovIdPlace = ()=> import('@/view/covIdPlace/index');
// 风险等级
const RiskLevel = ()=> import('@/view/riskLevel/index');
// 出行政策
const TravelPolicy = ()=> import('@/view/TravelPolicy/index');
// 封控查询
const Containment = ()=> import('@/view/Containment/index');
// 抗议求助
const CovIdHelp = ()=> import('@/view/CovIdHelp/index');
// 抗议快讯
const CovIdNews = ()=> import('@/view/CovIdNews/index');
// 一键求助
const OneClickHelp = ()=> import('@/view/OneClickHelp/index');
// 就医查询
const Medical = ()=> import('@/view/Medical/index');
// 防疫电话
const CovIdTelephone = ()=> import('@/view/CovIdTelephone/index');

// 查询风险疫情地区
const RiskArea = ()=> import('@/view/riskLevel/riskArea/riskArea');
// 国外疫情
const CovAbroad = ()=> import('@/view/domesticAbroad/cov-abroad');
const CovDomestic = ()=> import('@/view/domesticAbroad/covDomestic/cov-domestic');
// 国内疫情
const Domestic = ()=> import('@/view/domesticAbroad/components/banner/domestic');
// 国外疫情
const BerAbroad = ()=> import('@/view/domesticAbroad/components/banner/berAbroad');

const details = ()=> import('@/view/TravelPolicy/trip/tripDom/details');

Vue.use(VueRouter)

const routes = [
    {
        path:'/',
        redirect:'/home'
    },
    { 
        path: '/home', 
        component:Home 
    },
    { 
        path: '/nucleicAcid', 
        component:NucleicAcid 
    },
    { 
        path: '/holdbox', 
        component:Holdbox 
    },
    { 
        path: '/covIdPlace', 
        component:CovIdPlace 
    },
    { 
        path: '/riskLevel', 
        component:RiskLevel,
    },
    { 
        path: '/travelPolicy', 
        component:TravelPolicy 
    },
    { 
        path: '/details/:content', 
        name:'details',
        component:details 
    },
    { 
        path: '/containment', 
        component:Containment 
    },

    { 
        path: '/covIdHelp', 
        component:CovIdHelp 
    },
    { 
        path: '/covIdNews', 
        component:CovIdNews 
    },
    { 
        path: '/oneClickHelp', 
        component:OneClickHelp 
    },
    { 
        path: '/medical', 
        component:Medical 
    },
    { 
        path: '/covIdTelephone', 
        component:CovIdTelephone 
    },
    { 
        path: '/riskArea', 
        component:RiskArea 
    },
    { 
        path: '/covAbroad/:citycode', 
        component:CovAbroad,
    },
    { 
        path: '/covDomestic/:citycode/:diff', 
        name:'CovDomestic',
        component:CovDomestic 
    },
]
export default new VueRouter({
    routes
});