<template>
  <div class="cov-bulletin">
    <div class="cov-bulletin-img">
      <img width="100%" height="100%" src="@/assets/images/JinRiKuaiXun.png" alt="">
    </div>
    <div class="cov-Journalism">
      <van-swipe class="my-swipe" :touchable="false" :autoplay="3000" indicator-color="white" :vertical="true"
        :loop="true">

        <van-swipe-item v-for="(item,index) in list" :key="index">
          <router-link class="covIdNews" to="/covIdNews">
            {{item.item.info.title}}
          </router-link>
            
          
        </van-swipe-item>
      </van-swipe>
      <i class="iconfont icon-xiayiyeqianjinchakangengduo"></i>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { Swipe, SwipeItem } from 'vant';
import { ProtestFlash } from '@/api/indexCovid';
Vue.use(Swipe);
Vue.use(SwipeItem);
export default {
  data() {
    return {
      list: []
    }
  },
  mounted() {
    this.Protest();
  },
  methods: {
    async Protest() {
      let ProtestDAta = await ProtestFlash(this.page);
      let listData = ProtestDAta.data.components[1].data;
      this.list = listData;
    },
  },
}
</script>
<style lang="less" scoped>
.cov-bulletin {
  height: 112px;
  background-color: #fff;
  padding: 10px 20px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width:100%;
  box-sizing: border-box;

  .cov-bulletin-img {
    width: 96px;
    height: 92px;
  }

  .cov-Journalism {
    width: 634px;
    height: 72px;
    overflow: hidden;
    position: relative;
    i{
        position: absolute;
        right:5px;
        top:50%;
        transform: translateY(-50%);
        font-size:30px;
      }
  }
}

.my-swipe {
  height: 72px !important;

  .van-swipe-item {

    font-size: 30px;
    text-align: center;
    height: 72px !important;

    .covIdNews{
      color: #111;
      text-decoration: none;
      padding:0 30px;
      box-sizing: border-box;
      text-overflow: -o-ellipsis-lastline;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      line-clamp: 2;
      -webkit-box-orient: vertical;
      text-align: left;
      display: flex;
      align-items: center;
      height:100%;
      

    }
  }
}
</style>