<template>
  <div class="wrap">
    <div class="plague_hd">
      <div class="plague_hd_content">
        <h3 class="title">实时动态追踪</h3>
        <h3 class="title2">新冠肺炎疫情</h3>
      </div>
    </div>

    <van-sticky class="van-sticky" @scroll="scrollTo">
      <div class="sticky d-flex d-cont-start align-item">
        <button @click="scrollToPosition(0)" :class="[active == 0?'active':'']">全国疫情</button>
        <button @click="scrollToPosition(1)" :class="[active == 1?'active':'']">世界疫情</button>
      </div>
    </van-sticky>
    <!-- 全国疫情 -->
    <National id="region0"/>
    <!-- 海外疫情 -->

    <Overseas id="region1"/>
  </div>
</template>
<script>
// 国内
import National from '@/view/home/NationalEpidemic/National'
// 国外
import Overseas from '@/view/home/OverseasEpidemic/overseas'
export default {
  name: 'Home',
  data(){
    return{
      active:0
    }
  },
  components: {
    National,
    Overseas
  },
  methods: {
    scrollToPosition(e){
      this.active = e;
      document.getElementById(`region${e}`).scrollIntoView({behavior: "smooth",block: "start"})
    },
    scrollTo(e){
      if(e.scrollTop <= 6044){
        this.active = 0;
      }else{
        this.active = 1;
      }
    }
  },
}
</script>
<style lang="less" scoped>
.wrap {
  width: 750px;
  margin: 0 auto;

  .plague_hd {
    width: 100%;
    position: relative;
    padding-top: 37.3333333333333%;
    background: url('@/assets/images/banner.png') center top / 100% no-repeat;

    .plague_hd_content {
      position: absolute;
      top: 0;
      color: #FFF;
      top: 120px;
      left: 30px;

      .title {
        font-size: 25px;
        font-weight: normal;
      }

      .title2 {
        font-size: 52px;
        text-shadow: 0 4px 0 #789ce9;
      }
    }
  }

  .sticky {
    background-color: rgb(56 118 241);
    height: 90px;
    button{
      margin: 0 10px;
    font-size:22px;
    height:50px;
    background: hsla(0,0%,100%,.15);
    padding: 0 20px;
    color: hsla(0,0%,100%,.8);
    border-radius:25px;
    }
    .active{
      color:rgb(56 118 241);
      background-color:#FFF;
      
    }
  }
}
</style>