<template>
    <div class="chinaMap">
        <div id="main"></div>
    </div>
</template>

<script>
import { SinaData } from '@/api/indexCovid';
export default {
    data() {
        return {

        }
    },
    mounted() {
        this.handleData();
    },
    methods: {
        async handleData() {
            let data = await SinaData();
            let existing = [];
            data.data.list.forEach((item) => {
                // 获取现存数据
                existing.push({
                    name: item.name,
                    value: item.econNum
                })
            })
            this.$myecharts.chinaMap('main', existing);
            this.$bus.$emit('cityData',data.data.list);
        }
    },
}
</script>
<style lang="less" scoped>
.chinaMap {
    #main {
        width:714px;
        height: 600px;
        margin:0 auto;
        overflow: hidden;
        background-color: #FFF;
    }
}
</style>